<template>
  <div id="app" class="darken " v-cloak>
    <div v-if="loading">
      <h1>Loading...</h1>
    </div>

    <div id="content" v-else :class="period">
      <div class="input-group mb-3">
        <input
          @keyup.enter="showWeather()"
          type="text"
          class="form-control input-city"
          v-model="query"
          placeholder="введите город"
          aria-label="Recipient's username"
          aria-describedby="button-addon2"
        />
        <div class="input-group-append">
          <button
            :disabled="!query.length"
            @click="showWeather()"
            class="btn btn-outline-dark"
            type="button"
            id="button-addon2"
          >Поиск</button>
        </div>
      </div>
      <br />
      <h1>
        <img src="./assets/location.png" width="48px" />
        {{location}}
      </h1>
      {{desc}}
      <br />
      <hr />
      <p>
        <img :src="icon" alt="Weather icon" />
        <span class="temp">{{temp}} C&deg;</span>
        <br />
        <img src="./assets/low.svg" />
        {{temp_low}} C&deg;
        <img src="./assets/high.svg" />
        {{temp_high}} C&deg;
        <br />
        <img src="./assets/humidity.png" />
        {{humidity}}%
        <br />
        <img src="./assets/wind-speed.png" />
        {{windSpeed}} м/с
        <img src="./assets/wind.png" />
        {{windDeg}}
      </p>
      <hr />
    </div>
  </div>
</template>

<script>
import axios from "axios";

export default {
  name: "App",
  data: () => ({
    date: new Date(),
    windSpeed: null,
    windDeg: null,
    humidity: null,
    loading: true,
    query: "",
    lat: null,
    lon: null,
    location: null,
    temp: null,
    desc: "",
    temp_low: null,
    temp_high: null,
    icon: "",
    images: {
      rain: ["clouds.jpg"]
    }
  }),
  created() {
    navigator.geolocation.getCurrentPosition(pos => {
      console.log("got coordinates", pos.coords);
      this.lat = pos.coords.latitude;
      this.lon = pos.coords.longitude;
      this.loadWeather();
    });
  },
  methods: {
    // cityAvtocomplit(){
    // axios.get('http://api.travelpayouts.com/data/ru/cities.json')
    // .then(response => {
    // 	console.log('avtocomplite',response);
    // 	this.query = response.v.name;

    // })
    // },
    showWeather() {
      axios
        .get(
          `https://api.openweathermap.org/data/2.5/weather?q=${this.query}&units=metric&&appid=c45e3c465982da5e0946524d9b970800&lang=ru`
        )
        .then(response => {
          console.log("response", response);
          this.location = response.data.name + ", " + response.data.sys.country;
          this.temp = response.data.main.temp;
          this.temp_low = response.data.main.temp_min;
          this.temp_high = response.data.main.temp_max;
          this.desc = response.data.weather[0].description;
          this.humidity = response.data.main.humidity;
          this.windSpeed = response.data.wind.speed;
          this.loading = false;
          this.icon = `http://openweathermap.org/img/w/${response.data.weather[0].icon}.png`;
          this.query = "";
          var deg = response.data.wind.deg;
          if (deg <= 22.5) return "С";
          else if (deg <= 67.5) return (this.windDeg = "СВ");
          else if (deg <= 112.5) return (this.windDeg = "В");
          else if (deg <= 157.5) return (this.windDeg = "ЮВ");
          else if (deg <= 202.5) return (this.windDeg = "Ю");
          else if (deg <= 247.5) return (this.windDeg = "ЮЗ");
          else if (deg <= 292.5) return (this.windDeg = "З");
          else if (deg <= 337.5) return (this.windDeg = "СЗ");
          else if (deg <= 360) return (this.windDeg = "С");
        })
        .catch(e => {
          console.error(e);
          alert("Город не найден");
        });
    },

    loadWeather() {
      axios
        .get(
          `https://api.openweathermap.org/data/2.5/weather?lat=${this.lat}&lon=${this.lon}&units=metric&appid=c45e3c465982da5e0946524d9b970800&lang=ru`
        )
        .then(response => {
          console.log("response", response);
          this.location = response.data.name + ", " + response.data.sys.country;
          this.temp = response.data.main.temp;
          this.temp_low = response.data.main.temp_min;
          this.temp_high = response.data.main.temp_max;
          this.desc = response.data.weather[0].description;
          this.humidity = response.data.main.humidity;
          this.windSpeed = response.data.wind.speed;
          //   this.windDeg = response.data.wind.deg;
          this.loading = false;
          this.icon = `http://openweathermap.org/img/w/${response.data.weather[0].icon}.png`;
          var deg = response.data.wind.deg;
          if (deg <= 22.5) return "С";
          else if (deg <= 67.5) return (this.windDeg = "СВ");
          else if (deg <= 112.5) return (this.windDeg = "В");
          else if (deg <= 157.5) return (this.windDeg = "ЮВ");
          else if (deg <= 202.5) return (this.windDeg = "Ю");
          else if (deg <= 247.5) return (this.windDeg = "ЮЗ");
          else if (deg <= 292.5) return (this.windDeg = "З");
          else if (deg <= 337.5) return (this.windDeg = "СЗ");
          else if (deg <= 360) return (this.windDeg = "С");
        })
        .catch(e => {
          console.error(e);
        });
    }
  },
  computed: {
    period() {
      let hour = this.date.getHours();
      
      if (hour > 5 && hour < 9) {
        return "app-rassvet";
      } else if (hour > 9 && hour < 16) {
        return "app-day";
      } else if (hour > 16 && hour < 22) {
        return "app-zakat";
      } else return "app-night";
    }
  }
};
</script>

